/** @format */

.App {
  background-color: #24282f;
  font-family: "Roboto", sans-serif;
}
.moviecards {
  width: 16%;
  margin-top: 40px;
  margin-left: 30px;
  transition: transform 450ms;

  position: relative;
  margin-bottom: 30px;

  box-shadow: 0 -2px 10px rgba(0, 0, 0, 1);
}
.moviecards > img {
  width: 100%;
  height: 100%;
}
.Appcontainer {
  margin-top: 200px;
  margin: 0 80px;
  display: flex;
  flex-wrap: wrap;
}
.moviecards:hover {
  transform: scale(1.08);
}
.desc {
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 1;

  background-color: #000000;

  width: 100%;
  opacity: 0;
  transition: all 450ms;
  display: flex;
  justify-content: center;
  width: 100%;
}
.desc > h5 {
  align-items: center;
  color: aliceblue;
  font-family: "Raleway", sans-serif;
  padding: 5px;
}
.moviecards:hover .desc {
  opacity: 1;
}
.heighlight {
  border: 4px solid white;
  transition: transform 450ms;
}
.inputdesign {
  width: 500px;
  height: 35px;
  outline: none;
  color: rgb(255, 255, 255);
  padding: 5px;
  padding-left: 10px;
  border-radius: 18px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 1);
  background-color: #24282f;
}
.inputcont {
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
input,
input::-webkit-input-placeholder {
  font-size: 18px;
}
